@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Poppins:wght@400;500;600;700;800&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    scroll-behavior: smooth;
  }
  
  body {
    @apply font-sans text-gray-800 bg-gradient-to-br from-gray-50 to-gray-100;
  }
  
  h1, h2, h3, h4, h5, h6 {
    @apply font-heading font-bold;
  }
}

@layer components {
  .container-custom {
    @apply container mx-auto px-4 sm:px-6 lg:px-8 max-w-7xl;
  }
  
  .btn-primary {
    @apply bg-gradient-to-r from-primary-500 to-primary-600 hover:from-primary-600 hover:to-primary-700 text-white font-semibold py-3 px-6 rounded-lg shadow-lg hover:shadow-xl transform hover:-translate-y-0.5 transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-opacity-50;
  }
  
  .btn-secondary {
    @apply bg-white hover:bg-gray-50 text-secondary-600 font-semibold py-3 px-6 rounded-lg border border-gray-200 shadow hover:shadow-md transform hover:-translate-y-0.5 transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-secondary-500 focus:ring-opacity-50;
  }
  
  .card {
    @apply bg-white rounded-xl shadow-lg p-6 hover:shadow-xl transition-shadow duration-300;
  }
  
  .card-neu {
    @apply bg-white rounded-xl shadow-neu p-6 hover:shadow-neu-lg transition-shadow duration-300;
  }
  
  .section {
    @apply py-16 md:py-24;
  }
  
  .section-title {
    @apply text-3xl md:text-4xl lg:text-5xl font-bold mb-6 md:mb-10 text-center;
  }
  
  .section-subtitle {
    @apply text-xl md:text-2xl font-medium mb-12 text-center text-gray-600 max-w-3xl mx-auto;
  }
  
  .feature-card {
    @apply flex flex-col items-center p-6 bg-white rounded-xl shadow-lg hover:shadow-xl transition-all duration-300 border border-gray-100;
  }
  
  .testimonial-card {
    @apply bg-white p-6 rounded-xl shadow-lg border border-gray-100;
  }
  
  .price-tag {
    @apply inline-flex items-center justify-center px-4 py-1 rounded-full bg-accent-100 text-accent-700 font-semibold text-sm;
  }
  
  .guarantee-badge {
    @apply flex items-center justify-center w-24 h-24 rounded-full bg-white shadow-lg border-4 border-accent-500 text-center p-2;
  }
}

/* Animations */
.fade-in {
  animation: fadeIn 0.5s ease-in forwards;
}

.slide-up {
  animation: slideUp 0.5s ease-out forwards;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from { transform: translateY(20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

/* Glass effect */
.glass {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

/* Gradient text */
.gradient-text {
  @apply bg-clip-text text-transparent bg-gradient-to-r from-primary-500 to-secondary-500;
}
